.wp-block-blockylicious-piccy-gallery{
	.edit-mode{
		padding: 40px;
		background: #eee;
		position:relative;
		box-sizing: border-box;

		.piccy-label{
			position: absolute;
			top:0;
			left:0;
			padding: 8px;
			background: #ddd;
			font-weight: bold;
			text-transform: uppercase;
			font-size: 10px;
		}

		.piccy-gallery-inner-blocks{
			display:grid;
			grid-template-columns: repeat(3, 1fr);
			gap: 10px;
		}
	}
}